.main-banner {
	position: relative;
	background-repeat: no-repeat;
	background-position: center;
	background-size: cover;
	overflow: hidden;
	z-index: 1;
	height: 1085px;

	&:after {
		content: "";
		height: 100%;
		width: 100%;
		left: 0;
		bottom: 0;
		position: absolute;
		// background-image: url(../../images/main-banner/shape1.png);
		background-repeat: no-repeat;
		background-position: bottom left;
		background-size: 100%;
		z-index: -1;
	}

	h1 {
		margin-bottom: 50px;
		font-weight: 800;
		line-height: 1.3;
		color: $dark;
		font-size: 55px;
	}

	.btn-lg {
		padding: 20px 45px;
	}

	.title-ext {
		margin: 0 0 20px;
		font-size: 21px;
		font-weight: 700;
		display: inline-block;
		padding: 5px 15px;
		background-position: center;
		background-repeat: repeat;

		&.text-primary {
			background-image: url(../../images/shap/ext-blue.png);
		}

		&.text-secondary {
			background-image: url(../../images/shap/ext-orange.png);
		}
	}

	.tag-list {
		li {
			display: inline-block;
			margin-right: 8px;
			position: relative;
			padding-right: 5px;
			font-size: 14px;
			color: $dark;
			font-weight: 600;

			&:after {
				content: ",";
				position: absolute;
				bottom: 0;
				right: 0;
				color: #777777;
			}

			&.title {
				font-weight: 600;

				&:after {
					content: none;
				}
			}

			a {
				text-transform: capitalize;
				font-weight: 400;
				color: $gray;

				&:hover {
					color: $primary;
				}
			}
		}
	}

