	.inner-content {
		position: absolute;
		left: 50%;
		top: 50%;
		transform: translate(-50%, -50%);
	}

	@include respond('laptop') {
		height: 800px;

		.banner-img {
			margin-right: 0;
			margin-left: 0px;
		}

		h1 {
			margin-bottom: 30px;
			font-size: 45px;
		}

		.title-ext {
			margin: 0 0 10px;
			font-size: 18px;
		}
	}

	@include respond('tab-land') {
		height: 700px;

		h1 {
			margin-bottom: 30px;
			font-size: 36px;
		}

		.title-ext {
			font-size: 16px;
			margin: 0 0 15px;
		}
	}

	@include respond('tab-port') {
		height: 500px;

		h1 {
			font-size: 28px;
			margin-bottom: 20px;
		}

		.title-ext {
			font-size: 14px;
			margin: 0 0 10px;
			padding: 3px 10px;
		}

		.btn-lg {
			padding: 12px 24px;
			font-size: 14px;
		}

	}

	@include respond('phone-land') {
		.banner-img {
			margin-right: -40px;
			margin-left: -20px;
		}

		h1 {
			font-size: 24px;
			margin-bottom: 20px;
		}

		.main-banner {
			height: 500px;
		}

	}

	@include respond('phone') {
		height: auto;
		padding-top: 100px;

		.banner-img {
			margin: 30px 10px;
		}

		.inner-content {
			position: unset;
			transform: unset;
		}
	}

	// shapes ****
