	}

	.pt-img4 {
		bottom: 15%;
		left: 41%;
		z-index: 1;

		@include respond('tab-port') {
			bottom: 10%;
			left: 45%;
		}
	}

	.pt-img5 {
		bottom: 150px;
		right: 150px;
	}

