.thumb-wraper {
	display: inline-block;
	position: relative;
	width: 500px;

	@include respond('phone') {
		padding: 15px;
		width: 96vw;
	}

	.bg-img {
		position: relative;
		z-index: -2;
	}

	ul {
		li {
			position: absolute;

			a {
				width: 100px;
				height: 100px;
				border-radius: 50%;
				overflow: hidden;
				display: block;
				box-shadow: 0px 10px 60px 0px rgba(31, 34, 120, 0.1);
				@include transitionMedium;
				position: relative;

				img {
					width: 100%;
					height: 100%;
					object-fit: cover;
				}
			}

			&:after {
				content: "";
				height: 100%;
				width: 100%;
				left: 0;
				top: 0;
				position: absolute;
				background-color: $primary;
				@include transitionMedium;
				z-index: -1;
				transform: scale(0);
				border-radius: 50%;
			}

			&.active {
				&:after {
					transform: scale(1.14);
				}
			}

			&:nth-child(1) {
				top: 90px;
				left: 0px;
			}

			&:nth-child(2) {
				top: -20px;
				left: 45%;
				transform: translateX(-50%);
			}

			&:nth-child(3) {
				top: 85px;
				right: 40px;
			}

			&:nth-child(4) {
				right: 45px;
				bottom: 100px;
			}

			&:nth-child(5) {
				bottom: 20px;
				left: 45%;
				transform: translateX(-50%);
			}

			&:nth-child(6) {
				left: 10px;
				bottom: 115px;
			}

			@include respond('phone') {

				&:nth-child(2) {
					top: 10px;
					left: 50%;
				}

				&:nth-child(1) {
					left: 40px;
				}

				&:nth-child(5) {
					bottom: 32px;
					left: 50%;
				}

				&:nth-child(6) {
					left: 30px;
				}

				a {
					width: 70px;
					height: 70px;
				}
			}
		}
	}
}

