.testimonial-bx {
	position: relative;
	border-radius: 40px 40px 40px 40px;
	box-shadow: 0px 0px 30px rgba(185, 67, 0, 0.1);
	background-color: $secondary;
	padding: 50px;
	margin: 10px 20px 20px;

	&:after {
		content: "\f10e";
		position: absolute;
		right: 40px;
		bottom: 20px;
		font-family: "Font Awesome 5 Free";
		font-weight: 900;
		font-size: 92px;
		color: #fff;
		opacity: 0.3;
		line-height: 92px;
	}

	.testimonial-content {
		p {
			line-height: 28px;
			font-size: 18px;
			color: $white;
			font-weight: 400;
			// font-style: italic;
			margin-bottom: 30px;
		}
	}

	.client-info {
		.name {
			font-weight: 600;
			color: #fff;
			margin-bottom: 8px;
			font-size: 22px;
		}

		p {
			font-weight: 600;
			color: $white;
			font-size: 14px;
			text-transform: uppercase;
			margin-bottom: 0;
		}
	}

	.quote-icon {
		position: absolute;
		top: -50px;
		left: 50px;

		i {
			font-size: 36px;
			color: #f17732;
			width: 75px;
			height: 75px;
			text-align: center;
			background-color: #fff;
			line-height: 75px;
			border-radius: 20px;
			box-shadow: 0px 10px 60px 0px rgba(31, 34, 120, 0.1);
		}
	}

	.quote-icon-2 {
		position: absolute;
		top: -50px;
		left: 50px;

		i {
			font-size: 36px;
			color: #565acf;
			width: 75px;
			height: 75px;
			text-align: center;
			background-color: #fff;
			line-height: 75px;
			border-radius: 20px;
			box-shadow: 0px 10px 60px 0px rgba(31, 34, 120, 0.1);
		}
	}

	@include respond('tab-land') {
		border-radius: 30px 0 30px 30px;
		padding: 50px 30px 30px;

		.testimonial-content p {
			line-height: 26px;
			font-size: 16px;
		}

		.quote-icon {
			top: -45px;
			left: 30px;
		}
	}

	@include respond('phone') {
		margin: 50px 0px 0px;

		&:after {
			font-size: 50px;
			line-height: 72px;
		}

		.quote-icon {
			top: -35px;

			i {
				font-size: 28px;
				width: 65px;
				height: 65px;
				line-height: 65px;
			}
		}
	}
}

