.testimonial-slide {
	padding-bottom: 75px;

	.slick-arrow {
		height: 50px;
		width: 50px;
		border-radius: 12px;
		background-color: #ffe4d5;
		color: $secondary;
		font-size: 24px;
		bottom: 0;
		top: auto;
		@include transitionMedium;
		font-size: 0;

		&:before {
			content: "";
			font-family: 'Line Awesome Free';
			font-weight: 900;
			color: var(--primary);
			font-size: 24px;
		}

		&:hover {
			background-color: $secondary;
			color: #fff;
		}
	}

	.slick-prev {
		left: 20px;

		&:before {
			content: "\f060";
		}
	}

	.slick-next {
		right: auto;
		left: 85px;

		&:before {
			content: "\f061";
		}
	}

	.swiper-pagination {
		right: 30px;
		display: inline-block;
		text-align: right;
		left: auto;
		width: auto;
		bottom: 0;
		color: $secondary;
		font-weight: 700;

		.swiper-pagination-current {
			font-size: 25px;
		}

		.swiper-pagination-total {
			font-size: 15px;
			opacity: 0.5;
		}
	}

	@include respond('phone') {
		padding-bottom: 100px;

		.slick-prev {
			left: 0;
		}

		.slick-next {
			left: 60px;
		}

		.swiper-pagination {
			right: 10px;
		}
	}
}

